@import "_variables.scss";

*:hover,
*:focus {
  outline: none;
}

.appbar {
  width: 100%;
  height: 65px;
  line-height: 60px;
  overflow: hidden;
  color: #000;
  > div {
    width: 95%;
    margin: 0 auto;
    height: 100%;
    > section {
      display: inline-block;
      > a {
        min-width: 50px;
        height: 40px;
        line-height: 42px;
        padding: 0px 8px 0px 8px;
        text-decoration: none;
        text-transform: uppercase;
        text-align: center;
        font-size: 14px;
        font-weight: 500;
        margin-right: 5px;
        font-family: $font;
        &.icon {
          text-transform: none;
          width: 50px;
          height: 50px;
          padding: 0px;
          margin: 0;
          text-align: center;
          border-radius: 50%;
          > i {
            font-size: 24px;
            position: relative;
            top: .5em;
          }
        }
      }
      &.left {
        width: auto;
        height: 100%;
        display: flex;
        float: left;
        align-items: center;
        justify-content: initial;
        overflow: hidden;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        > .title {
          margin-left: 10px;
        }
      }
      &.right {
        width: auto;
        height: 100%;
        display: flex;
        float: right;
        align-items: center;
        justify-content: flex-end;
        overflow: hidden;
      }
      > .title {
        font-size: 16px;
        color: inherit;
        vertical-align: middle;
        font-family: "Roboto", "Segoe UI", BlinkMacSystemFont, system-ui,
          -apple-system;
      }
      > button {
        margin-top: 0px;
        color: inherit;
        margin-left: 5px;
        > i {
          vertical-align: middle;
        }
        &[open-menu="nesting"] {
          display: none;
        }
      }
    }
  }
}

.badge[data-badge]{
  position: relative;
  &:after{
    content: attr(data-badge);
    width: 22px;
    height: 22px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    font-weight: 600;
    font-size: 12px;
    align-content: center;
    align-items: center;
    position: absolute;
    top: -4px;
    right: -4px;
    background: #ff0026;
    color: #ffffff !important;
    border-radius: 50%;
  }
}

@import "_mixins.scss";
@import "_variables.scss";

.btn {
  margin-top: 5px;
  padding: 12px 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  background: color(blue);
  color: color(white);
  border: 0px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 500;
  transition: all 150ms linear;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.12);
  > i {
    font-size: 15px;
    vertical-align: middle;
    margin-top: -2px;
    transition: all 0.5s linear 0s;
    position: relative;
    &:nth-child(1) {
      position: relative;
      left: -5px;
    }
    &[ani],
    &[move] {
      left: 0px;
      right: 0px;
    }
  }
  &:hover {
    transition: all 150ms linear;
    opacity: 0.9;
    > i {
      &[ani="left"] {
        @include animation(ani-left 1s infinite);
        @include keyframes(ani-left) {
            10% { left: 0px;}
            50%{ left: -5px}
         };
      }
      &[ani="right"] {
        @include animation(ani-right 1s infinite);
        @include keyframes(ani-right) {
            10% { left: 0px;}
            50%{ left: 5px}
         };
      }
      &[ani="rotate"] {
        @include animation(ani-rotate 1s infinite);
        @include keyframes(ani-rotate) {
            from{ transform: rotate(0deg); }
            to{ transform: rotate(360deg); }
         };
      }

      &[move="left"] {
        @include animation(ani-left 1s 1);
      }
      &[move="right"] {
        @include animation(ani-right 1s 1);
      }
      &[move="rotate"] {
        @include animation(ani-rotate 1s 1);
      }
    }
  }
  &:active {
    transition: all 150ms linear;
    opacity: 0.7;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.14),
      0 3px 3px -2px rgba(0, 0, 0, 0.2), 0 1px 8px 0 rgba(0, 0, 0, 0.12);
  }
  &.outline {
    background: transparent;
    border: color(blue) 1px solid;
    color: #222;
    @include hoverColor(color(blue), color(grey, dark, 3));
  }
  &.text{
    background: transparent;
    box-shadow: none;
  }
  &.round, 
  &.transparent{
  width: 50px;
  height: 50px;
  padding: 0px;
  margin: 0;
  text-align: center;
  border-radius: 50%;
  > i {
    font-size: 20px;
    left: 0;
    right: 0;
  }
  &:before {
    content: "";
    width: 30px;
    height: 30px;
    background: rgba(color(blue), 0.8);
    border-radius: 50%;
    margin: -6px -5.5px;
    display: inline-block;
    position: absolute;
    transform: scale(0);
    transition: all 0.5s linear 0s;
  }
}
}

.link {
  margin-top: 5px;
  padding: 10px 15px;
  text-decoration: none;
  background: transparent;
  box-shadow: none;
  color: color(grey, dark, 3);
  font-size: 14px;
  font-weight: bold;
  line-height: 1.3;
  font-family: Tahoma, Geneva, sans-serif;
}

btn.btn[ts] {
  background: transparent;
  text-align: center;
  box-shadow: none;
  > i {
    font-size: 20px;
  }
  &:hover {
    box-shadow: none;
  }
}


