@import "_mixins.scss";
@import "_variables.scss";



@each $name, $sets in $colors {
  @each $type, $set in $sets {
    @if $type == 'base' {
      .#{$name} {
        background: $set !important;
      }
      .text-#{$name} {
        color: $set !important;
      }
    }
    @else{
      @each $key, $value in $set {
        .#{$name}-#{$type}-#{$key} {
          background: $value !important;
        }
        .text-#{$name}-#{$type}-#{$key} {
          color: $value !important;
        }
      }
    }
  }
}


@mixin colorClass($name) {
  &.outline {
    color: color($name) !important;
    background: transparent !important;
    border: color($name, base) 1px solid !important;
    &:hover {
      color: #fff !important;
      background: color($name) !important;
    }
  }

  &.text {
    background: transparent !important;
    color: color($name) !important;
  }

  &.checkbox {
    background: transparent !important;
    > input {
      &:checked {
        background: color($name) !important;
        & + span::before {
          border-color: color($name) !important;
          background: color($name) !important;
        }
      }
      &:active {
        & + span::before {
          border-color: color($name) !important;
        }
      }
    }
  }

  &.radio {
    background: transparent !important;
    > span{
      &::after{
        background-color: color($name);
      }
    }
    > input{
      &:checked {
         background-color: color($name);
         & + span::before {
           border-color: color($name);
         }
      }
    }
  }

  &.switch {
    background: transparent !important;
    > input {
      &:checked {
        background-color: color($name);
        & ~ span {
          &:before {
            background: rgba(color($name), 0.54) !important;
          }
          &:after {
            background: color($name) !important;
            border-color: color($name) !important;
          }
        }
      }
      &:active + span::before {
        background-color: rgba(color($name), 0.4);
      }
    }
  }

  &.text {
    &:hover {
      background: rgba(color($name), 0.1) !important;
    }
    > .rippleWave {
      background: rgba(color($name), 0.2) !important;
    }
  }
  &.range {
    background: color($name);
    &::-webkit-slider-thumb {
      background: color($name);
    }
  }
}

$base: ();
@each $name, $type in $colors {
  @if $name !=black {
    $base: append($base, $name);
  }
}

@include build-color-class($base);

@import "_mixins.scss";
@import "_variables.scss";


$modal-width: 320px;
$modal-height: 160px;
$modal-maxWidth: 85% !important;

$alert-modal-width: 280px;
$alert-modal-height: 118px;

$scroll-modal-width: 500px;
$scroll-modal-height: auto;

$full-modal-width: 100%;
$full-modal-height: 100vh;

$sheet-modal-width: 100%;
$sheet-modal-height: calc(100vh / 1.5);

$dark-grey: color(grey, dark, 3);
$light-grey: color(grey, light, 1);

$font: "Roboto", "Segoe UI", BlinkMacSystemFont, system-ui, -apple-system;

.dialog_overlay {
  position: fixed;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.loader_overlay {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  pointer-events: none;
  background: rgba(0, 0, 0, 0.8);
  z-index: 6;
  &.light{
    background: rgba(255,255,255, 0.8);
    .md-loader{
      circle{
        stroke: blue;
      }
    }
  }
}

.dialog {
  * {
    &:focus,
    &:hover {
      outline: none;
    }
  }
  visibility: hidden;
  width: $modal-width;
  height: $modal-height;
  max-width: $modal-maxWidth;
  color: black;
  background: #fff;
  line-height: normal;
  position: fixed;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: 5px;
  -webkit-box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2),
    0px 6px 10px 0px rgba(0, 0, 0, 0.14), 0px 1px 18px 0px rgba(0, 0, 0, 0.12);
  box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2),
    0px 6px 10px 0px rgba(0, 0, 0, 0.14), 0px 1px 18px 0px rgba(0, 0, 0, 0.12);
  pointer-events: auto !important;
  overflow: hidden;
  font-family: $font;
  z-index: 2;
  > header,
  section,
  footer {
    width: calc(100% - (24 * 2px));
    margin: 0px auto;
  }
  > header {
    height: auto;
    padding-top: 20px;
    > .title {
      font-size: 18px;
      color: $dark-grey;
      font-family: inherit;
      font-weight: bold;
      &:first-letter {
        text-transform: capitalize;
      }
    }
    > .subtitle {
      display: block;
      font-size: 14px;
    }
  }
  > section {
    height: calc(100% - 95px);
    color: $light-grey;
    font-size: 16px;
    overflow: hidden;
    scroll-behavior: smooth;
  }
  > .text-field {
    width: 100%;
  }
  footer {
    width: 100%;
    height: 52px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    background: color(white, base);
    > a {
      min-width: 30px;
      height: 35px;
      padding: 0px 8px 0px 8px;
      text-decoration: none;
      line-height: 35px;
      text-transform: uppercase;
      text-align: center;
      cursor: pointer;
      font-size: 14px;
      font-weight: 500;
      margin-right: 8px;
      font-family: inherit;
      font-weight: 560;
      border-radius: 5px;
      transition: all 0s !important;
      &.ripple {
        transition: all 0s;
      }
      &.left {
        justify-content: flex-start;
        position: absolute;
        margin-left: 15px;
        left: 0px;
      }
    }
  }

  &.alert {
    width: $alert-modal-width;
    height: $alert-modal-height;
    > header {
      display: none;
    }
    > section {
      height: 100%;
      padding-top: 8px;
    }
  }

  &.scrollable {
    width: $scroll-modal-width;
    height: calc(100vh - 10%);
    > header {
      height: 40px;
      > .title {
        font-weight: 500;
        font-size: 20px;
      }
    }
    > section {
      padding-left: 25px;
      padding-right: 25px;
      border-top: 1px solid ;
      border-bottom: 1px solid $light-grey;
      overflow-y: scroll;
      &::-webkit-scrollbar {
        width: 10px;
      }

      &::-webkit-scrollbar-track {
        background: color(white, base);
      }

      &::-webkit-scrollbar-thumb {
        background: $light-grey;
      }
    }
  }

  &.full {
    width: $full-modal-width;
    height: $full-modal-height;
    min-width: $full-modal-width;
    visibility: visible !important;
    border-radius: 0px;
    position: fixed;
    top: none;
    bottom: -200%;
    transition: bottom 0.4s;
    > header {
      padding-top: 0;
      width: 100%;
    }
    &.active {
      bottom: 0%;
    }
  }

  &.sheet {
    width: $sheet-modal-width;
    height: $sheet-modal-height;
    min-width: $full-modal-width;
    visibility: visible !important;
    border-radius: 8px 8px 0px 0px;
    position: fixed;
    top: auto;
    bottom: -200%;
    transition: bottom 0.2s;
    &.active {
      bottom: 0%;
    }
    > header {
      height: 55px;
      > .title {
        font-size: 22px;
      }
    }
    > footer {
      position: fixed;
      bottom: 0px;
    }
    > section {
      overflow-y: scroll;
      border-top: 0.5px solid $light-grey;
      height: calc(100% - 80px);
      padding-left: 25px;
      padding-right: 20px;
      &::-webkit-scrollbar {
        width: 10px;
      }

      &::-webkit-scrollbar-track {
        background: color(white, base);
      }

      &::-webkit-scrollbar-thumb {
        background: $light-grey;
      }
      footer {
        position: static !important;
        > a:last-child {
          margin-right: 2px;
        }
      }
    }
  }

  &.alert,
  &.scrollable,
  &.full {
    footer {
      position: absolute;
      bottom: 0px;
    }
  }

  &.loader {
    width: 230px;
    height: 95px;
    text-align: center;
    visibility: visible !important;
    position: relative;
    top: 50%;
    color: color(white, base);
    transform: translateY(-50%);
    box-shadow: none;
    background: transparent;
    > main {
      margin: 0 auto;
    }
    &.small, &.medium{
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &.colored{
      background: color(white, base);
      -webkit-box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2),
        0px 6px 10px 0px rgba(0, 0, 0, 0.14), 0px 1px 18px 0px rgba(0, 0, 0, 0.12);
      box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2),
