    font-size: 16px;
    line-height: 28px;
    border: none;
    border-bottom: 1px solid;
    border-radius: 0;
    white-space: nowrap;
    cursor: pointer;
    border-radius: 4px 4px 0 0;
    -webkit-appearance: none;
    -moz-appearance: none;
    > option {
      background: #fff;
    }
    &:focus,
    &.has-value {
      &:focus {
        & ~ .floating-label {
          color: $primary_color;
        }
        & ~ .line-ripple {
          transform: scaleX(1);
        }
      }
      & ~ .floating-label {
        top: 8px;
        font-size: 12px;
        will-change: top, font-size, color;
      }
    }
  }
  > .helper {
    font-size: 12px;
