    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    position: absolute;
    bottom: -20px;
  }
  &.fill {
    .floating-label{
      left: 12px;
    }
    > select {
      padding: 20px 26px 4px 12px;
      background: #f5f5f6;
    }
  }
  &.outlined{
    > .floating-label {
      left: 12px;
      transition: all 0.1s;
    }
    > select{
      padding: 20px 26px 12px 12px;
      border: none;
      height: 56px;
      line-height: 20px;
      background: transparent;
      border: 1.8px solid #c2c2c2;
      font-size: 16px;
      border-radius: 6px;
      &:hover {
        background: transparent;
      }
      &:focus {
        background: transparent;
        border: 2px solid $primary_color;
      }
      &.has-value{
        & ~ .floating-label {
          top: -7px;
          left: 10px;
          font-size: 12px;
          will-change: top, font-size, color;
          background: #fff;
          padding: 4px;
        }
      }
    }
  }
  .lead {
    position: absolute;
    top: 17px;
    left: 15px;
    & ~ select {
      padding-left: 45px !important;
    }
    & ~ .floating-label {
      left: 45px;
    }
  }
  .trail {
    position: absolute;
    top: 18px;
    right: 5px;
    & ~ select{
      padding-right: 45px !important;
    }
  }
}

